.exercise-card {
    width: 400px;
    height: 445px;
    background: #fff;
    border-top: 4px solid #FF2625;
    border-bottom-left-radius: 20px;
    text-decoration: none;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding-bottom: 10px;
    transform: scale(1, 1);
    transition: 0.3s all ease-in-out;
  }
  
  .exercise-card img {
    height: 326px;
  }
  .exercise-card:hover {
    transform: scale(1.1, 1.1);
  }
  .exercise-card {
    width: 320px;
  }
  .cardLink {
    text-decoration: none;
  }
  .cardBtn:hover {
    background-color: rgb(14 55 96 / 50%) !important;
  }

  @media screen and (max-width:400px) {
    .exercise-card {
      width: 280px;
    }
  }